import { base64Encode } from '@@addons/functions/base64';
import { MemberClockingInfo_I, MemberClockingInfo_S } from '@@addons/interfaces/attendance/member_clocking_info';
import { MemberClockingInfoFull_I, MemberClockingInfoFull_S } from '@@addons/interfaces/attendance/member_clocking_info/index_full';
import '@@addons/widgets/profile_photo';
import { LitElement, html, css } from 'lit';
import { customElement, property, query, queryAll } from 'lit/decorators.js'


@customElement("clocker-table-parts-first")
export class ClockerTablePartsFirst extends LitElement {
  constructor() { super(); }

  private _data: any = null;

  public set data(value: any) {
    this._data = value;
    this.requestUpdate();
  }

  public get data(): any {
    return this._data;
  }

  private _type: any = null;

  public set type(value: any) {
    this._type = value;
    this.requestUpdate();
  }

  public get type(): any {
    return this._type;
  }

  private clockingInfo: MemberClockingInfoFull_I | MemberClockingInfo_I = null;

  @property({ type: Boolean })
  private fullInfo?: boolean = false;

  @property({ type: String })
  private clocking_info?: string = "";

  @property({ type: String })
  private memberProfileBaseUrl: string = "/member/member-profile?view=";

  connectedCallback() {
    super.connectedCallback();

    const _clockingInfo = JSON.parse(this.clocking_info);
    // console.log({ "_clockingInfo": _clockingInfo });

    if (_clockingInfo !== null) {
      if ("attendance" in _clockingInfo &&
        "additionalInfo" in _clockingInfo &&
        "lastSeen" in _clockingInfo &&
        "status" in _clockingInfo) {
        this.fullInfo = true;
        this.clockingInfo = MemberClockingInfoFull_S(_clockingInfo);
      } else {
        this.fullInfo = false;
        this.clockingInfo = MemberClockingInfo_S(_clockingInfo);
      }
      // console.log({ "this.clockingInfo": this.clockingInfo });
    }
  }

  disconnectedCallback() { }
  static styles = [
    css`
   :host { display: block; }
  `
  ];
  
  private get getClockingInfo(): MemberClockingInfo_I {
    // @ts-ignore
    return this.clockingInfo;
  }
  
  private get getClockingInfoFull(): MemberClockingInfoFull_I {
    // @ts-ignore
    return this.clockingInfo;
  }

  render() {
    const fullInfo = this.fullInfo,
      clockingInfo = this.getClockingInfo,
      clockingInfoFull = this.getClockingInfoFull;
    // console.log({ "clockingInfo": clockingInfo });
    const MEMBER_ID = base64Encode(String(fullInfo? clockingInfoFull.attendance.memberId.id: clockingInfo.memberId.id), true);
    return html`<div class="flex flex-col whitespace-normal justify-between content-between">
  <div class="d-block m-1">
    <span class="badge badge-info shadow-lg p-2 m-1">
      ${fullInfo? clockingInfoFull.attendance.clockingMethodName: clockingInfo.clockingMethodName}
    </span>
  </div>
  <div class="flex m-1 justify-evenly">
    <user-profile-photo class="w-32 h-32 mr-1" rounded 'click-to-open'=""
      click-to-open="${this.memberProfileBaseUrl}${MEMBER_ID}" type="member"
      url="${fullInfo? clockingInfoFull.attendance.memberId.profilePicture: clockingInfo.memberId.profilePicture}" size="32"></user-profile-photo>
  </div>
  <div class="block m-1">
    <span class="shadow p-1 block bg-white rounded text-dark">
      <b>Name</b>: ${fullInfo? clockingInfoFull.attendance.memberId.firstname: clockingInfo.memberId.firstname} ${fullInfo? clockingInfoFull.attendance.memberId.middlename: clockingInfo.memberId.middlename}
      ${fullInfo? clockingInfoFull.attendance.memberId.surname: clockingInfo.memberId.surname}
    </span>
  </div>
</div>`;
  }

  firstUpdated() { }

  createRenderRoot() {
    return this;
  }
}
